.header{
    display: flex;
    align-items: center;
    justify-content: space-between;
    padding: 20px;
    position: sticky;
    top: 0;
    z-index: 100;
    background-color: white;

}
.header-logo{
    height: 25px;
    object-fit: contain;
    margin-left: 20px;
}
.header-search{
    display: flex;
    align-items: center;
    width: 40%;
    border: 1px solid lightgrey;
}
.header-search > input {
    flex: 1;
    border: none;
}
.header-searchbtn{
    width: 50px !important;
    background-color: #fafafa;
    border-left: 1px solid lightgray;
    color: gray;
}

.header-right{
    display: flex;
    align-items: center;
}
.header-icon{
    margin-right: 8px;
}
.header-icon:hover{
    cursor: pointer;
    color: red;
}